"use client"

import type React from "react"

import { useState } from "react"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Mail, CheckCircle2 } from "lucide-react"

export function Newsletter() {
  const [email, setEmail] = useState("")
  const [isSubmitted, setIsSubmitted] = useState(false)

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    // TODO: Implement newsletter subscription logic
    setIsSubmitted(true)
    setTimeout(() => setIsSubmitted(false), 3000)
  }

  return (
    <section id="newsletter" className="container mx-auto px-4 py-20 md:py-32">
      <div className="max-w-2xl mx-auto">
        <Card className="border-2">
          <CardHeader className="text-center">
            <div className="mx-auto mb-4 w-12 h-12 rounded-full bg-primary/10 flex items-center justify-center">
              <Mail className="w-6 h-6 text-primary" />
            </div>
            <CardTitle className="text-3xl md:text-4xl text-balance">Subscribe to My Newsletter</CardTitle>
            <CardDescription className="text-base leading-relaxed">
              Get weekly insights on web development, career tips, and curated resources delivered straight to your
              inbox. No spam, unsubscribe anytime.
            </CardDescription>
          </CardHeader>
          <CardContent>
            <form onSubmit={handleSubmit} className="flex flex-col sm:flex-row gap-3">
              <Input
                type="email"
                placeholder="your.email@example.com"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
                className="flex-1"
                disabled={isSubmitted}
              />
              <Button type="submit" size="lg" disabled={isSubmitted}>
                {isSubmitted ? (
                  <>
                    <CheckCircle2 className="w-4 h-4 mr-2" />
                    Subscribed!
                  </>
                ) : (
                  "Subscribe"
                )}
              </Button>
            </form>
            <p className="text-xs text-muted-foreground text-center mt-4">
              Join 5,000+ developers who trust my weekly newsletter
            </p>
          </CardContent>
        </Card>
      </div>
    </section>
  )
}
